.label {
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  text-align: left;
  margin-bottom: 10px;
}

.container {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 30px;
}

.column {
  display: flex;
  width: 50%;
  flex: 1 0 auto;
  flex-direction: column;
}
