$active-color: rgba(238, 201, 44, 1);
$main-theme-color: rgba(111, 60, 20, 1);
:root {
  --main-background-color: #ffffff; /* Светлая тема */
  --main-text-color: #000000;
  --active-color: rgba(238, 201, 44, 1);
  --extra-theme-color: rgba(247, 235, 229, 1);
  --main-theme-color: rgba(111, 60, 20, 1);
  --background-header: rgba(35, 33, 32, 0.7);
  --header-logo-color: rgba(246, 218, 135, 1);
  --header-acc-color: white;
  --active-navlink-bg: rgba(246, 218, 135, 1);
  --active-navlink-text-color: rgba(111, 60, 20, 1);
  --navlink-text-color: #ffffff;
  --card-bg: #fff;
  --card-price-bg: rgba(111, 60, 20, 1);
  --card-price-text-color: white;
  --card-top-info-bg: white;
  --card-top-info-text-color: rgba(111, 60, 20, 1);
  --card-status-bg: rgba(238, 201, 44, 1);
  --card-status-text-color: rgba(111, 60, 20, 1);
  --hall-info-text-color: rgba(111, 60, 20, 1);
  --slider-theme-color: rgba(111, 60, 20, 1);
  --slider-rail-color: rgba(247, 235, 229, 1);
  --datepicker-bg: rgba(247, 235, 229, 1);
  --datepicker-button-bg: rgba(111, 60, 20, 1);
  --datepicker-day-color: var(--main-text-color);
  --input-color: rgba(111, 60, 20, 1);
  --input-bg: rgba(247, 235, 229, 1);
  --text-color-label: rgba(111, 60, 20, 1);
  --checkbox-bg: rgba(111, 60, 20, 1);
  --info-list-bg: white;
  --skeleton-bg: rgba(0, 0, 0, 0.11);
  --basket-status-bg: rgba(238, 201, 44, 1);
  --basket-status-color: rgba(111, 60, 20, 1);
}

[data-theme="dark"] {
  --main-background-color: #281608; /* Темная тема */
  --extra-theme-color: rgba(246, 218, 135, 1);
  --main-text-color: white;
  --background-header: rgb(49 43 27 / 60%);
  --navlink-text-color: #f6da87;
  --header-logo-color: rgba(246, 218, 135, 1);
  --header-acc-color: white;
  --active-navlink-bg: rgba(246, 218, 135, 1);
  --active-navlink-text-color: #281608;
  --card-bg: rgba(111, 60, 20, 1);
  --card-price-bg: rgba(246, 218, 135, 1);
  --card-price-text-color: rgba(111, 60, 20, 1);
  --card-top-info-bg: rgba(111, 60, 20, 1);
  --card-top-info-text-color: white;
  --card-status-bg: rgba(49, 27, 10, 1);
  --card-status-text-color: var(--extra-theme-color);
  --hall-info-text-color: var(--extra-theme-color);
  --slider-theme-color: rgba(246, 218, 135, 1);
  --slider-rail-color: rgba(84, 64, 7, 1);
  --datepicker-bg: rgba(111, 60, 20, 1);
  --datepicker-button-bg: rgba(49, 27, 10, 1);
  --datepicker-day-color: var(--main-text-color);
  --input-bg: rgba(246, 218, 135, 1);
  --text-color-label: rgba(246, 218, 135, 1);
  --checkbox-bg: rgba(111, 60, 20, 1);
  --info-list-bg: rgba(111, 60, 20, 1);
  --skeleton-bg: rgba(255, 255, 255, 0.11);
  --basket-status-bg: rgba(49, 27, 10, 1);
  --basket-status-color: white;
}

@font-face {
  font-family: "Museo Sans Cyrl";
  src: local("MuseoSansCyrl-500"), url("../shared/fonts/MuseoSansCyrl-500.eot");
  src: url("../shared/fonts/MuseoSansCyrl-500.eot?#iefix")
      format("embedded-opentype"),
    url("../shared/fonts/MuseoSansCyrl-500.woff") format("woff"),
    url("../shared/fonts/MuseoSansCyrl-500.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../shared/fonts/MuseoSansCyrl-700.eot");
  src: local("MuseoSansCyrl-700"),
    url("../shared/fonts/MuseoSansCyrl-700.eot?#iefix")
      format("embedded-opentype"),
    url("../shared/fonts/MuseoSansCyrl-700.woff") format("woff"),
    url("../shared/fonts/MuseoSansCyrl-700.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Museo Sans Cyrl" !important;
  font-weight: inherit;
  box-sizing: border-box;
  transition: background-color 365ms cubic-bezier(0.215, 0.61, 0.355, 1),
    filter 365ms cubic-bezier(0.215, 0.61, 0.355, 1),
    opacity 365ms cubic-bezier(0.215, 0.61, 0.355, 1),
    color 365ms cubic-bezier(0.215, 0.61, 0.355, 1);
}

body {
  min-height: 100vh;
  font-weight: normal;
  background-color: var(--main-background-color);
}
