.container {
  .card {
    width: 100%;
    padding: 14px 16px 20px 16px;
    border-radius: 24px;
    background-color: var(--card-bg);

    box-shadow: 0px 4px 43.1px 0px rgba(111, 60, 20, 0.26);
  }
}

.media {
  aspect-ratio: 270/209;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
}
