@import "../../../App/index.scss";

.header {
  position: sticky;
  width: 100%;
  top: 0;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  overflow: hidden;
  z-index: 10;

  button.linkAcc {
    display: flex;
    align-items: center;
    color: var(--header-acc-color);
    font-size: 16px;
    font-weight: 500;
    line-height: 19.2px;
    text-align: left;

    span {
      display: block;
      margin-left: 8px;
      text-transform: capitalize;
    }

    .arrow {
      display: block;
      margin-left: 8px;

      path {
        stroke: var(--header-acc-color);
      }
    }
  }
}

.top {
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.superContainer {
  width: 100%;
}

.container {
  padding-left: 30px;
  padding-right: 30px;

  @media (max-width: 350px) {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(2px);
  transform: scale(1.01);

  img {
    z-index: -2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--background-header);
  }
}

.logo {
  path {
    fill: var(--header-logo-color);
  }
}
