.container {
  // Применяем стили для контейнера здесь, если нужно
  padding: 12px;
  border-radius: 15px;
  background-color: var(--datepicker-bg);
  .date {
    // Добавляем класс для кнопок
    width: 34px;
    height: 34px;
    min-height: 0;
    min-width: 0;
    padding: 0;
    background-color: var(--datepicker-button-bg);
    color: white;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: center;
    border-radius: 10px;

    @media (max-width: 380px) {
      width: 24px;
      height: 24px;
    }

    &:hover {
      background-color: var(--datepicker-button-bg);
    }

    &.active {
      background-color: var(--active-color);
      color: var(--main-theme-color);
    }

    &.pastDate {
      opacity: 0;
    }
  }
}

.weekDays {
  display: grid;
  grid-template-columns: repeat(7, 34px);

  @media (max-width: 380px) {
    grid-template-columns: repeat(7, 24px);
  }
  gap: 7px;
  margin-bottom: 10px;
  justify-content: space-between;

  .weekDay {
    text-align: center;
    font-size: 12px;
    font-weight: 400;
    line-height: 14.4px;
    text-align: center;
    color: var(--main-text-color);
  }
}

.dates {
  display: grid;
  grid-template-columns: repeat(7, 34px);

  @media (max-width: 380px) {
    grid-template-columns: repeat(7, 24px);
  }
  justify-content: space-between; // Применяем отступ между элементами
  gap: 10px;
}
