.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 30px;

  @media (max-width: 350px) {
    padding: 0 15px;
  }
  div.card {
    max-width: 160px;
    margin: 30px 0 0 0;
  }

  button.continue {
    margin: 30px 0 35px 0;
  }
}

.text {
  color: var(--main-text-color);
  display: block;
  margin: 20px 0 0 0;
  text-align: center;
}
