.fade-enter {
  opacity: 0;
  transition: 300ms; /* Длительность перехода */
}

.fade-enter-active {
  opacity: 0.3;
  transition: 300ms; /* Длительность перехода */
}

.fade-exit {
  opacity: 0.3;
  transition: 300ms; /* Длительность перехода */
}

.fade-exit-active {
  opacity: 0.3;
  transition: 300ms; /* Длительность перехода */
}
