.container {
  padding: 0 30px;
  margin: 30px 0 0 0;

  @media (max-width: 350px) {
    padding: 0 15px;
  }
}

.top {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  display: flex;

  .togglerSect {
    background: var(--main-theme-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 auto;
    min-height: 31px;
    border-radius: 15px;
    button {
      padding: 0 12px;
      font-size: 16px;
      font-weight: 700;
      line-height: 19.2px;
      text-align: left;
      color: white;
      min-height: 31px;
      border-radius: 15px;

      &.active {
        color: var(--active-color);
      }
    }
  }
}

.settingBtn {
  svg {
    width: 18px;
    height: 18px;
  }
}
