.container {
  background-color: var(--card-bg);
  box-shadow: 0px 4px 37.1px 0px rgba(111, 60, 20, 0.17);
  padding: 35px 20px;
  margin: 30px 0 0 0;
  border-radius: 15px;

  --text-color-info-block: #6f3c14;
}

.container[data-theme="dark"] {
  --text-color-info-block: #f6da87;
}

.list {
  display: flex;
  flex-direction: column;

  svg {
    display: block;
    margin-right: 12px;
  }

  li {
    align-items: center;
    display: flex;
    color: var(--text-color-info-block);
    &:first-child {
      margin: 0;
    }

    margin: 30px 0 0 0;

    strong {
      font-weight: 700;
    }
  }
}
