@import "../../App/mixins.scss";
button.price {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  background-color: var(--card-price-bg);
  color: var(--card-price-text-color);
  min-height: 47px;
  position: relative;
  overflow: hidden;
  margin: 10px 0 0 0;

  span {
    z-index: 1;
  }

  @include animatedGradientBackground();
}

button.price:hover {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  background-color: var(--card-price-bg);
  color: var(--card-price-text-color);
  min-height: 47px;
  position: relative;
  overflow: hidden;
  margin: 10px 0 0 0;

  span {
    z-index: 1;
  }

  @include animatedGradientBackground();
}


button.price[data-theme="dark"] {
  &::after {
    animation: none;
  }
}
