@import "../../App/mixins.scss";
.menu {
  width: 100%;
  max-height: 100%;
  min-height: 250px;
  position: relative;

  .closeBtn {
    position: absolute;
    right: 30px;
    min-width: 0;
    min-height: 0;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    top: 40px;

    @include main-button-styles;
  }

  .leftHeaderButton {
    position: absolute;
    left: 30px;
    border-radius: 50%;
    top: 40px;

    @include main-button-styles;
  }
}

.header {
  width: 100%;
  text-align: center;
  @include section-header-text-styles;
  margin: 43px 0 0 0;
}
