.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  min-width: 183px;
  min-height: 101px;
  z-index: 1;
  top: 15%;
  margin: 0 auto;

  path {
    fill: rgb(246, 218, 135);
  }
}



.loading {
  color: rgb(246, 218, 135);
  font-size: 20px;
  font-weight: 700;
  margin-top: 30px;
  clip-path: inset(0 1.3ch 0 0);
  animation: l 1s steps(12) infinite;
}

@keyframes l {
  to {
    clip-path: inset(0 -1ch 0 0);
  }
}
