.container {
  display: flex;
  align-items: center;
  margin: -10px 0 25px 0;

  button.link,
  button.link:hover {
    background-color: transparent;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 47px;
    min-width: 60px;
    border-radius: 15px;
    border: 2px solid var(--navlink-text-color);
    color: var(--navlink-text-color);
    margin-left: 8px;
    width: 33%;

    @media (max-width: 374px) {
      font-size: 16px;
    }

    &:first-child {
      margin: 0;
    }

    &.active {
      background: var(--active-navlink-bg);
      color: var(--active-navlink-text-color);
      border: 0;
    }
  }
}
