.container {
  display: flex;
  position: relative;
}

.input {
  border: 0;
  outline: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--extra-theme-color);
  color: var(--input-color);
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  text-align: center;
  padding: 0 10px;
  min-height: 40px;
  border-radius: 15px;
  width: 100%;
}

.selectCont {
  position: absolute;
  display: flex;
  z-index: 0;
  left: 0;
  right: 0;
  width: 100%;
  top: 43px;
  height: 0px;
  overflow: hidden;

  border-radius: 15px;

  transition: 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  position: absolute;
  &::after {
    position: absolute;
    content: "";
    top: 0;
    left: 50%;
    height: 100%;
    width: 1px;
    background: var(--input-color);
    opacity: 0.2;
    left: 50%;
    transform: translateX(-50%);
  }

  &.open {
    z-index: 10;
    height: 98px;
  }
  ul {
    width: 50%;

    height: 100%;
    overflow: scroll;
    position: relative;

    &:first-child {
    }

    li {
      padding: 15px 0;
      display: flex;
      justify-content: center;
      color: var(--input-color);
      background-color: var(--extra-theme-color);

      &.active {
        background-color: var(--main-theme-color);
        color: white;
      }
    }
  }
}
