@import "../../App/mixins.scss";
.top {
  display: flex;
  align-items: center;
  justify-content: space-between;

  --form-theme-color: var(--main-theme-color);
  --counter-text-color: var(--main-theme-color);

  button {
    border: 2px solid var(--form-theme-color);
    min-width: 0;
    width: 47px;
    height: 47px;
    border-radius: 15px;

    span {
      color: var(--form-theme-color);
    }

    &:hover {
      border: 2px solid var(--form-theme-color);
      color: var(--form-theme-color);
    }

    svg {
      path {
        fill: var(--form-theme-color);
      }
    }
  }
}

.top[data-theme="dark"] {
  --form-theme-color: rgba(246, 218, 135, 1);
  --counter-text-color: var(--main-background-color);
}
.counter {
  background-color: var(--extra-theme-color);
  flex: 1 0 auto;
  margin: 0 15px;
  text-align: center;
  min-height: 47px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  font-family: Museo Sans Cyrl;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  overflow: hidden;
  position: relative;
  max-width: 100%;

  @include animatedGradientBackground(
    200%,
    rgba(238, 201, 44, 0.2),
    rgba(111, 60, 20, 0.2),
    1.5s,
    -50%
  );

  @media (max-width: 374px) {
    font-size: 16px;
    line-height: 19px;
  }

  @media (max-width: 360px) {
    font-size: 12px;
    line-height: 14px;
  }
}
