.superContainer {
  position: relative;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  filter: grayscale(0.22) brightness(0.6) blur(2px);
  transform: scale(1.015);

  img {
    width: 100%;
    height: 100%;
  }
}

.logo {
  min-width: 183px;
  min-height: 101px;
  z-index: 1;
  position: absolute;
  top: 15%;

  path {
    fill: rgba(246, 218, 135, 1);
  }

  @media (max-height: 700px) {
    top: 10%;
  }

  @media (max-height: 550px) {
    position: relative;
    top: 0;
  }
}
