.container {
  padding: 0 30px;

  @media (max-width: 350px) {
    padding: 0 15px;
  }

  .sliderPrice {
    margin: 30px 0 0 0;
  }

  div.header {
    margin: 30px 0 0 0;
  }

  button.submit {
    margin: 35px 0 30px 0;
  }
}

.checkboxs {
  display: flex;
  gap: 10px;
  margin: 30px 0 0 0;
  justify-content: space-between;

  .column {
    width: 50%;

    .label {
      display: block;
      font-family: Museo Sans Cyrl;
      font-size: 16px;
      font-weight: 400;
      line-height: 19.2px;
      text-align: left;
      color: var(--main-text-color);
    }
  }
}

.checkboxList {
  display: flex;
  flex-direction: column;
  margin: 5px 0 0 0;

  li {
    margin: 15px 0 0 10px;

    span {
      overflow-wrap: break-word;
      word-break: break-all;
    }
  }
}
