@import "../../../../App/mixins.scss";

.form {
  margin: 25px 0 0 0;
  position: sticky;
  background-color: var(--main-background-color);
  bottom: 0;
  z-index: 2;
  --form-theme-color: var(--main-theme-color);
  --counter-text-color: var(--main-theme-color);
  padding: 20px 30px;
  box-shadow: 0px 4px 37.1px 0px rgba(111, 60, 20, 0.17);

  @media (max-width: 350px) {
    padding: 20px 15px;
  }
}

.form[data-theme="dark"] {
  --form-theme-color: rgba(246, 218, 135, 1);
  --counter-text-color: var(--main-background-color);
}

.top {
  display: flex;
  align-items: center;
  justify-content: space-between;

  button {
    border: 2px solid var(--form-theme-color);
    min-width: 0;
    width: 47px;
    height: 47px;
    border-radius: 15px;

    span {
      color: var(--form-theme-color);
    }

    &:hover {
      border: 2px solid var(--form-theme-color);
      color: var(--form-theme-color);
    }

    svg {
      path {
        fill: var(--form-theme-color);
      }
    }
  }
}

.counter {
  background-color: var(--extra-theme-color);
  flex: 1 0 auto;
  margin: 0 15px;
  text-align: center;
  min-height: 47px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  font-family: Museo Sans Cyrl;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  overflow: hidden;
  position: relative;
  max-width: 100%;

  @include animatedGradientBackground(
    200%,
    rgba(238, 201, 44, 0.2),
    rgba(111, 60, 20, 0.2),
    1.5s,
    -50%
  );
}

button.selectBtn {
  margin: 24px 0 0 0;
  transition: 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);

  &.disabled {
    background: #b9b9b9;

    &::after {
      animation: none;
      display: none;
    }
    span {
      color: rgba(255, 255, 255, 0.8);
    }
  }
}
