@import "../../App/mixins.scss";

.sectionProfName {
  margin: 90px 0 0 0;
}

.container {
  padding: 0 30px;
  --toggler-theme-color: var(--main-theme-color);
  @media (max-width: 350px) {
    padding: 0 15px;
  }
}

.container[data-theme="dark"] {
  --toggler-theme-color: rgba(246, 218, 135, 1);
}

.profileInfo {
  .phone {
    &.support {
      display: flex;
      justify-content: center;
    }
  }

  button {
    @include main-button-styles;
  }
}

.sectionToggleTheme,
.sectionHeaderHistory {
  margin: 30px 0 0 0;
}

.toggleTheme {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 30px 0 0 0;
  gap: 20px;

  button {
    min-height: 47px;
    border-radius: 15px !important;
    border: 2px solid var(--toggler-theme-color);
    border-left: 2px solid var(--toggler-theme-color) !important;
    padding: 0 10px;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    text-align: center;
    position: relative;
    overflow: hidden;
    color: var(--toggler-theme-color);

    &.active {
      color: white;
      background-color: var(--main-theme-color);
      &:hover {
        background-color: var(--main-theme-color);
      }
      border: 0;
      border-left: 0 !important;
      @include animatedGradientBackground();
    }

    .btnText {
      z-index: 1;
    }
  }
}

.sectionHeaderSupport {
  margin: 10px 0 0 0;
}

.lastElem {
  margin-bottom: 30px;
}

div.alert {
  color: white;
}

.humanSVG {
  svg {
    path {
      stroke: var(--text-color-info-block);
    }
  }
}

.phone {
  button {
    svg {
      margin: 0;
      path {
        fill: none;
        stroke: var(--main-theme-color);
      }
    }
  }
}

.profileInfo {
  li {
    .humanSVG {
      path {
        fill: var(--text-color-info-block);
      }
    }
  }
}

.phone {
  svg {
    path {
      stroke: var(--text-color-info-block);
    }
  }
}
