@mixin section-header-text-styles {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  text-align: center;
  color: var(--main-text-color);
}

@mixin animatedGradientBackground(
  $width: 200%,
  $bg-color-start: rgba(238, 201, 44, 0.2),
  $bg-color-end: rgba(111, 60, 20, 0.2),
  $animation-duration: 1.5s,
  $transform-percent: -100% // Новый параметр для трансформации
) {
  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: $width; // Параметр ширины
    height: 100%;
    background: linear-gradient(
      91.45deg,
      $bg-color-start -1.65%,
      $bg-color-end 100%
    );
    z-index: 0;
    animation: transformAnimation $animation-duration
      cubic-bezier(0.215, 0.61, 0.355, 1) infinite; // Добавляем анимацию
  }

  // Определяем анимацию внутри миксина
  @keyframes transformAnimation {
    0% {
      transform: translate(#{$transform-percent}); // Используем новый параметр
    }
    50% {
      transform: translate(0%);
    }
    100% {
      transform: translate(#{$transform-percent}); // Используем новый параметр
    }
  }
}

@mixin card-styles {
  width: 100%;
  padding: 14px 16px 20px 16px;
  border-radius: 24px;
  background-color: var(--card-bg);
  box-shadow: 0px 4px 43.1px 0px rgba(111, 60, 20, 0.26);
}

@mixin main-button-styles {
  min-width: 30px;
  min-height: 30px;
  background: var(--extra-theme-color);
  margin-left: 8px;
  border-radius: 50%;
  padding: 0;

  &:hover {
    background: var(--extra-theme-color);
  }
  span {
    color: var(--main-theme-color);
  }

  svg {
    path {
      fill: var(--main-theme-color);
    }
  }
}
