@import "../../../../../App/mixins.scss";

.container {
  display: grid;
  width: 100%;
  border-radius: 25px;
  overflow: hidden;
  box-shadow: 0px 4px 43.1px 0px rgba(111, 60, 20, 0.26);
  margin: 15px 0 0 0;
  touch-action: pan-y;
  grid-template-columns: 1fr auto;
}

button.deleteBtn {
  background-color: rgba(255, 77, 77, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  padding: 0;
  min-width: 0;
  width: 40px;
  max-width: 40px;
  color: white;
  overflow: hidden;

  &.mobile {
    width: 0px;
  }

  &:hover {
    background-color: rgba(255, 77, 77, 1);
  }

  svg {
    path {
      fill: white;
    }
  }
}

.card {
  @include card-styles;
  width: auto;
  display: flex;
  align-items: center;
  box-shadow: none;
  flex: 1 0 auto;
  border-radius: 0;
  --add-color: rgba(111, 60, 20, 1);
  --counter-text-color: white;
}
.card[data-theme="dark"] {
  --add-color: rgba(246, 218, 135, 1);
  --counter-text-color: rgba(111, 60, 20, 1);
}
.image {
  aspect-ratio: 1/1;
  width: 53px;
  border-radius: 10px;
}

.info {
  flex: 1 1 auto;
  margin: 0 12px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  line-height: 16.8px;
  text-align: left;
  color: var(--main-text-color);
}

.price {
  color: var(--add-color);
}
