@import "../../../App/index.scss";
@import "../../../App/mixins.scss";
.header {
  position: relative;

  width: 100%;

  z-index: 10;

  .avatar {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 60%);
    bottom: 0;
    width: 104px;
    height: 104px;
    border: 2px solid white;

    @media (max-width: 374px) {
      width: 75px;
      height: 75px;
    }
  }
}

.bgCont {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  overflow: hidden;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(2px);
  transform: scale(1.01);

  img {
    z-index: -2;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--background-header);
  }
}

.container {
  width: 100%;
  display: flex;
  min-height: 120px;
  padding: 0 20px;
  align-items: center;
  justify-content: space-between;
  gap: 20px;

  svg {
    flex: 0 0 auto;
    path {
      fill: var(--header-logo-color);
    }
  }
}

.name {
  @include section-header-text-styles;
  color: var(--header-acc-color);
  transform: translateY(5px);
  text-transform: uppercase;

  @media (max-width: 374px) {
    font-size: 18px;
  }
}
