.container {
  padding: 0 30px;
  flex: 1 0 auto;

  @media (max-width: 350px) {
    padding: 0 15px;
  }
}

button.submitBtn {
  width: 100%;
  line-height: 26.4px;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  color: var(--main-theme-color);
  padding: 0 30px;
  min-height: 60px;
  background: rgba(238, 201, 44, 1);
  margin: 45px 0 0 0;
  position: sticky;
  bottom: 0;
  z-index: 2;

  @media (max-width: 350px) {
    padding: 0 15px;
  }

  &:hover {
    background: rgba(238, 201, 44, 1);
  }
}

.cardBottom {
  width: 100%;
  display: flex;
  gap: 10px;

  button {
    background-color: transparent;
  }
}

.counter {
  margin: 20px 0 0 0;
}

div.card {
  margin: 20px 0 0 0;
}

div.datepicker {
  margin: 30px 0 0 0;
}

.label {
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  text-align: left;
  margin-bottom: 10px;
}

.lastElem {
  display: flex;
  width: 100%;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 30px;
  align-items: flex-end;

  .column {
    display: flex;
    flex-direction: column;
    flex: 1 0 auto;
    width: 50%;
    min-height: 48px;

    label {
      min-height: 48px;

      input {
        height: 48px;
      }
    }

    .activeTimePickerSelect {
      top: 52px;
    }
  }

  button.column {
    span {
      @media (max-width: 360px) {
        font-size: 16px;
        line-height: 19px;
      }

      @media (max-width: 340px) {
        font-size: 14px;
        line-height: 16px;
      }
    }
  }
}

.input {
  input {
    min-height: 45px;
  }
}
