.container {
  display: flex;
  flex-direction: column;
  padding: 0 30px;
  align-items: center;
  margin: 30px 0 0 0;
}

.name {
  width: 100%;
  max-width: 320px;
  height: 40px;
}

.loadingImage {
  margin: 30px 0 0 0;

  .loadingImageItem {
    width: 80% !important;
    border-radius: 15px;
    overflow: hidden;
    aspect-ratio: 326/249;
    max-width: 500px;

    span {
      height: 100%;
    }

    &:first-child {
      margin-left: 0;
    }
  }
}
