@import "../../App/index.scss";
@import "../../App/mixins.scss";

button.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $main-theme-color;
  position: relative;
  border-radius: 15px;
  color: white;
  font-weight: 700;
  width: 100%;
  font-size: 20px;
  line-height: 24px;
  min-height: 47px;
  padding: 0 10px;
  border: 0;
  outline: 0;
  overflow: hidden;

  &:hover {
    background: $main-theme-color;
  }

  span {
    z-index: 1;
    color: white;
  }

  @include animatedGradientBackground();
}
