.top {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  display: flex;

  .togglerSect {
    background: var(--main-theme-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 auto;
    min-height: 31px;
    border-radius: 15px;
    button {
      padding: 0 12px;
      font-size: 16px;
      font-weight: 700;
      line-height: 19.2px;
      text-align: left;
      color: white;
      min-height: 31px;
      border-radius: 15px;

      &.active {
        color: var(--active-color);
      }
    }
  }

  button.settingBtn {
    min-width: 0;
    border-radius: 50%;
    margin-left: 8px;
    min-width: 0;
    min-height: 0;
    width: 30px;
    height: 30px;
    flex: 0 0 auto;
    &:hover {
      background: var(--extra-theme-color);
    }
    background: var(--extra-theme-color);

    span {
      width: 30px;
      height: 30px;
      color: var(--main-theme-color);
    }

    svg {
      path {
        fill: var(--main-theme-color);
        z-index: 1;
      }
    }
  }
}
