img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

html,
body {
  min-height: 100vh;
}

input {
  background-color: transparent;
  outline: 0;
}

button {
  outline: 9;
  border: 0;
  cursor: pointer;
}

a {
  text-decoration: none;
}

#root {
  min-height: 100vh;
  width: 100%;
}

/* Также можно скрыть скроллбар для элементов с прокруткой */
* {
  scrollbar-width: none; /* Для Firefox */
}

*::-webkit-scrollbar {
  display: none; /* Для Chrome, Safari и Opera */
}

/*IE9*/
*::selection {
  background-color: transparent;
}
*::-moz-selection {
  background-color: transparent;
}
* {
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  /*IE10*/
  -ms-user-select: none;
  user-select: none;

  /*You just need this if you are only concerned with android and not desktop browsers.*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
input[type="text"],
textarea,
[contenteditable] {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.slide-enter {
  transform: translateX(100%); /* Сначала справа */
  transition: 300ms linear;
  position: absolute;
  width: 100%;
}

.slide-enter-done {
  position: relative;
}

.slide-enter-active {
  transform: translateX(0);
  opacity: 1;
  position: absolute;
  transition: 300ms linear;
  width: 100%;
}

.slide-exit {
  transform: translateX(0);
  transition: 300ms linear;
  opacity: 1;
  position: absolute;
  width: 100%;
}

.slide-exit-active {
  transform: translateX(-100%); /* Уходит влево */
  transition: 300ms linear;
  position: absolute;
  width: 100%;
}

ul,
li,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

