.form {
  display: flex;
  align-items: center;
  flex-direction: column;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 55px;
  width: 100%;

  @media (max-height: 550px) {
    position: relative;
    top: 0;
    transform: translateY(0);
    margin-top: 30px;
  }

  label {
    width: 100%;
    margin: 20px 0 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 55px;
    background-color: white;
    border-radius: 15px;
    input {
      outline: 0;
      border: 0;
      font-size: 20px;
      line-height: 24px;
      text-align: center;
    }
  }
}

button.submitBtn {
  margin: 20px 0 0 0;
}

.formHeader {
  font-family: "Museo Sans Cyrl";
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  text-align: center;
  color: white;
}

.formDesc {
  font-family: "Museo Sans Cyrl";
  font-size: 22px;
  font-weight: normal;
  line-height: 26px;
  text-align: center;
  color: white;
  margin: 40px 0 0 0;
}
