.desc {
  margin: 30px 0 0 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  text-align: center;
  color: var(--main-text-color);
}

.value {
  margin: 20px 0 0 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  text-align: center;

  --value-text-color: var(--main-theme-color);
  color: var(--value-text-color);
}

.value[data-theme="dark"] {
  --value-text-color: #f6da87;
}
