@import "../../App/mixins.scss";
div.card {
  @include card-styles;
  display: flex;
  flex-direction: column;
  border-radius: 24px;
  padding: 12px;
  --add-info-color: #747474;
  height: 100%;

  .media {
    aspect-ratio: 132/119;
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    flex: 0 0 auto;
  }

  .content {
    flex: 1 0 auto;
    color: var(--main-text-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 16.8px;
    text-align: center;
    padding: 0;
    margin: 12px 0 0 0;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      color: var(--add-info-color);
    }
  }

  button.basket {
    background-color: var(--basket-status-bg);
    color: var(--basket-status-color);
    transition: 0.25s;
    &::after {
      animation: none;
      display: none;
    }
  }
}

div.card[data-theme="dark"] {
  --add-info-color: rgba(246, 218, 135, 1);
}
