div.select {
  background-color: var(--input-bg);
  color: var(--input-color);
  border-radius: 15px;
  width: 100%;
  padding: 0 12px;
  border: inherit !important;
}

div.select__control {
  background-color: var(--input-bg);
  color: var(--input-color);
  border: 0;
  min-height: 40px;
  border: 0;
  box-shadow: none;

  &:hover {
    border: 0;
  }
}

span.select__indicator-separator {
  display: none;
}

div.select__value-container {
  font-size: 16px;
  font-weight: 400;
  line-height: 19.2px;
  padding-left: 10px;
  padding-right: 10px;
  color: var(--input-color);
}

.select__option {
  transition: 0.5s;
}

div.select__option--is-selected {
  background: var(--main-theme-color);
}

div.select__single-value {
  color: var(--input-color);
}
