.container {
  flex: 0 0 auto;
  display: flex;
  align-items: center;

  button {
    border-radius: 50%;
    color: var(--add-color);
  }
}

.counter {
  min-width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--add-color);
  border-radius: 50%;
  color: var(--counter-text-color);
  aspect-ratio: 1/1;
  margin: 0 5px;
  padding: 0 5px;
}
